.bilibili-live-recorder {
  display: flex;
  position: fixed;
  z-index: 9999;
  top: 100px;
  right: 100px;
  width: 180px;
  height: 60px;
  border-radius: 60px;
  overflow: hidden;
  user-select: none;
  color: #fff;
  backdrop-filter: saturate(180%) blur(20px);
  background-color: rgba(0, 0, 0, 0.75);
  box-shadow: 0 0 20px 0 rgba(0, 85, 255, 0.1); }
  .bilibili-live-recorder *,
  .bilibili-live-recorder *::before,
  .bilibili-live-recorder *::after {
    box-sizing: border-box; }
  .bilibili-live-recorder .blr-states {
    display: flex;
    width: 60px;
    height: 100%;
    font-size: 12px;
    padding: 5px;
    color: rgba(255, 255, 255, 0.8);
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3); }
    .bilibili-live-recorder .blr-states .blr-state {
      position: relative;
      display: none;
      height: 100%;
      width: 100%;
      justify-content: center;
      align-items: center;
      text-align: center;
      cursor: pointer;
      border-radius: 50%;
      transition: all 0.2s ease;
      transform: scale3d(1, 1, 1); }
      .bilibili-live-recorder .blr-states .blr-state.blr-active {
        display: flex; }
      .bilibili-live-recorder .blr-states .blr-state:hover {
        transform: scale3d(1.1, 1.1, 1.1); }
      .bilibili-live-recorder .blr-states .blr-state:active {
        transform: scale3d(0.9, 0.9, 0.9); }

@keyframes rotate {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
      .bilibili-live-recorder .blr-states .blr-state.blr-state-before-record {
        background-image: linear-gradient(0deg, #be1974, #ff1c68);
        border: 5px solid rgba(103, 10, 57, 0.8); }
      .bilibili-live-recorder .blr-states .blr-state.blr-state-recording {
        background-image: linear-gradient(0deg, #be1974, #ff1c68);
        border: 5px solid rgba(103, 10, 57, 0.8); }
        .bilibili-live-recorder .blr-states .blr-state.blr-state-recording::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          border-radius: 50%;
          border: 2px solid #ffeb3b;
          border-left: 2px solid rgba(255, 255, 255, 0);
          border-right: 2px solid rgba(255, 255, 255, 0);
          animation: rotate 3s linear infinite; }
      .bilibili-live-recorder .blr-states .blr-state.blr-state-after-record {
        background-image: linear-gradient(0deg, #007b94, #01b5da);
        border: 5px solid rgba(0, 57, 68, 0.8); }
        .bilibili-live-recorder .blr-states .blr-state.blr-state-after-record::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          border-radius: 50%;
          border: 2px solid #23dfe5;
          border-left: 2px solid rgba(255, 255, 255, 0);
          border-right: 2px solid rgba(255, 255, 255, 0);
          animation: rotate 3s linear infinite; }
  .bilibili-live-recorder .blr-monitor {
    display: flex;
    flex-direction: column;
    flex: 1;
    font-size: 12px;
    letter-spacing: 1px;
    padding: 6px 5px 6px 0;
    color: rgba(255, 255, 255, 0.75);
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.5); }
    .bilibili-live-recorder .blr-monitor .blr-monitor-top {
      display: flex;
      align-items: center;
      flex: 1;
      padding: 0 5px;
      line-height: 1;
      border-bottom: 1px solid rgba(255, 255, 255, 0.15); }
    .bilibili-live-recorder .blr-monitor .blr-monitor-bottom {
      display: flex;
      align-items: center;
      flex: 1;
      padding: 0 5px;
      line-height: 1; }
